.cardWrapper {
  margin: 20rem;
  width: 30rem;
  height: 40rem;
  text-align: center;
}
.cardMain {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  padding: 1rem;
  background: radial-gradient(white 5%, orange);
}
img {
  border-radius: 50%;
}
.cardHeader {
  display: flex;
  align-items: center;
  gap: 2rem;
}
